import Vue from 'vue'
import VueRouter from 'vue-router'
import Left from "@/components/Left";
import Center from "@/components/Center";
import Test from "@/components/Test";
Vue.use(VueRouter)

const rs=[
    {
        path:'/',
        alis:'/home',
        component:Left
    },
    {
        path: '/left',
        component: Left
    },
    {
        path: '/center',
        component: Center
    },
    {
        path: '/test',
        component:Test
    }
]

const router=new VueRouter({
    routes:rs
})
export default router
