<template>
    <div>
        <Header></Header>
        <div id="menu" v-show="active=='menu'">
            <el-collapse accordion>
                <el-collapse-item style="font-size: 1.2em" v-for="(i,index) in years" :title=i.year  :key=index>
                    <p style="font-size: 1.2em" v-for="(e,index) in i.events" @click="getEventById(i.year,e.id)" :id=i.year+e.id :key=index>{{e.title}}</p>
                </el-collapse-item>
            </el-collapse>
        </div>

        <div id="content" v-show="active=='content'">
            <Content @setActive="setActive" :event="event"></Content>
        </div>
    </div>
</template>

<script>
    import Header from "@/components/Header";
    import Content from "@/components/content/Content";
    export default {
        name: "Left",
        components: {Content, Header},
        data(){
            return{
                active:'menu',
                years:[
                   {
                       year:'2020',
                       events: [{name:"A",id:'1',},{name:"B",id:'2'}]
                   },
                   {
                       year:'2019',
                       events: [{name:"A",id:'1',},{name:"B",id:'2'}]
                   }
                ],
                event:undefined
            }
        },
        methods:{
            setActive(){
                this.active='menu'
            },
            getYearsAndEvents(){
                let mythis=this
                fetch("/b/yearsandevents.json").then(res=>res.json())
                .then(data=>{
                    console.dir(data)
                    mythis.years=data.years
                })
                .catch(err=>{
                    console.log(err)
                })
            },
            getEventById(year,eid){
                this.active='content'
                let mythis=this
                console.log(year+" "+eid)
                let url='/b/'+year+'/'+eid+'.json'
                fetch(url).then(res=>res.json())
                .then(data=>{
                    console.log(data)
                    mythis.event=data
                })
                .catch(err=>{
                    console.log(err)
                })
            }
        },
        mounted() {
            this.getYearsAndEvents()
        }
    }
</script>

<style scoped>
#menu{
    width: 80%;
    margin: 0 auto;
    padding: 1em 1em;
    background-color: #ffffff;
    border-radius: 4px;
}
p{
    text-align: center;
}
p:hover{
        opacity: 0.7;
        color: green;
        cursor: pointer;
    }

    #content{
        padding: 0 1em;
        border: 1px solid darkgrey;
        width: 80%;
        margin: 0 auto;
        border-radius: 4px;
    }
</style>
<style>
    body{
        background-color: #d7ccc8;
    }
</style>
